AAQ;IACR,YAAY;IACZ,8BAA8B;AAClC;;AAEA;IACI,WAAW;AACf;;AAEA;;IAEI,YAAY;IACZ,YAAY;IACZ,WAAW;IACX,iBAAiB;IACjB,eAAe;IACf,yBAAyB;IACzB,kBAAkB;IAClB,aAAa;IACb,kBAAkB;IAClB,yBAAyB;IACzB,mBAAmB;AACvB;;AAEA;;IAEI,gBAAgB;IAChB;AACJ;;AAEA;IACI,iBAAiB;IACjB,sBAAsB;IACtB,gBAAgB;IAChB,uBAAuB;IACvB,oBAAoB;IACpB,qCAAqC;IACrC,YAAY;IACZ,gBAAgB;IAChB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;GACG,gBAAgB;GAChB,kBAAkB;AACrB;;AAEA,6BAA6B;;AAE7B;IACI,mBAAmB;AACvB;;AAEA;IACI;MACE,UAAU;IACZ;EACF;;EAEA;IACE;MACE,0BAA0B;IAC5B;EACF;;EAEA;IACE;MACE,UAAU;IACZ;IACA;QACI,UAAU;IACd;EACF;;EAEA;IACE;MACE,wBAAwB;IAC1B;IACA;QACI;IACJ;EACF;;EAEA;IACE,iEAAiE;EACnE;;EAEA;IACE,8DAA8D;EAChE;;EAEA;IACE;;MAEE,eAAe;IACjB;EACF","sourcesContent":["/* css reset */\n\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n    -ms-overflow-style: none;\n    scrollbar-width: none;\n}\n\n*::-webkit-scrollbar {\n    display: none;\n}\n\nbutton {\n    color: inherit;\n    font: inherit;\n    outline: none;\n}\n\na {\n    text-decoration: none;\n}\n\nselect,\ntextarea,\nbutton,\ninput {\n    font-family: inherit;\n}\n\n/* css variables */\n\n:root {\n    --form-control-color: rebeccapurple;\n    --clr-text: #111827;\n    --clr-dark: #e2e2e2;\n    --clr-primary: #fa9639;\n    --clr-primary-semi-transparent:#fa963942;\n    --clr-toggle-shade: black;\n    --clr-accent: #f3f3f3;\n    --rotateIcon: 180deg;\n    --header-image: url(images/header-background1lightmode.svg);\n    color: #5e5e5e;\n}\n\n.darkmode {\n    --clr-text: #d7dce6;\n    --clr-dark: #111827;\n    --clr-primary: #bdadfd;\n    --clr-primary-semi-transparent:#bdadfd42;\n    --clr-toggle-shade: rgb(234, 226, 255);\n    --clr-accent: #1f2937;\n    --rotateIcon: 0deg;\n    --header-image: url(images/header-background1-darkmode.svg);\n    color: white;\n}\n\n/* main layout */\n\nbody {\n    height: 100svh;\n    display: grid;\n    grid-template-columns: auto 1fr;\n    grid-template-rows: 185px 1fr;\n    -webkit-font-smoothing: antialiased;\n    -moz-font-smoothing: antialiased;\n    -o-font-smoothing: antialiased;\n    user-select: none;\n}\n\n\n#sidebar {\n    width: 250px;\n    background-color: var(--clr-dark);\n    grid-row: 1 / 3;\n}\n\nsvg {\n\twidth: 24px;\n\theight: 24px;\n}\n\n#content {\n    background-color: var(--clr-accent);\n    background-repeat: no-repeat;\n    overflow-y: auto;\n}\n\ndialog {\n    left: 0;\n    right: 0;\n    top: 0;\n    bottom: 0;\n    margin: auto;\n    position: absolute;\n}\n\n\n/* style classes add via javascript */\n\nbody .low {\n    background-color: rgb(186, 255, 186);\n}\n\nbody .moderate {\n    background-color: rgb(255, 255, 153);\n}\n\nbody .high {\n    background-color: rgb(255, 181, 181);\n}\n\nbody.darkmode .high {\n    border: rgb(255, 28, 28) solid 2px;\n    background-color: var(--clr-dark);\n}\n\nbody.darkmode .moderate {\n    border: rgb(255, 255, 57) solid 2px;\n    background-color: var(--clr-dark);\n}\n\nbody.darkmode .low {\n    border: rgb(65, 255, 65) solid 2px;\n    background-color: var(--clr-dark);\n}\n\n.hide {\n    display: none;\n}\n\n.active {\n    background-color: var(--clr-primary) !important;\n}\n\n.folders .active:hover,\n#sidebar .active:hover{\n    background-color: var(--clr-primary);\n}\n\n.folderContainer.active button,\n#sidebar button.active {\n    color:var(--clr-dark) !important;\n}\n\n.rotate {\n    rotate: 180deg;\n}\n\n\n/* sidebar styling */\n\n\n#sidebar {\n    display: flex;\n    flex-direction: column;\n    padding: 1rem;\n    align-items: center;\n    overflow-y: auto;\n    transition: margin 0.3s ease;\n}\n\n\nbutton {\n    display: flex;\n    align-items: center;\n    outline: none;\n}\n\n\n.folderContainer {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    width: 200px;\n    margin-top: 8px;\n    border-radius: 8px;\n}\n\n.logo {\n    font-weight: bolder;\n    font-size: 2.25rem;\n    border-bottom: solid  4px;\n}\n\n.logo span {\n    color: var(--clr-primary);\n}\n\n.flex-container {\n    display: flex;\n    border: 1px solid #cccccc;\n    align-items: center;\n    gap: 4px;\n    margin-top: 28px;\n    padding: 4px;\n    border-radius: 12px;\n    font-weight: bold;\n    font-size: 14px;\n    width: 200px;\n}\n\n.flex-container svg {\n    width: 50px;\n    height: 50px;\n    border-radius: 50%;\n}\n\na {\n    height: 50px;\n    border-radius: 50%;\n    color: black;\n}\n\n\n.folderContainer :last-child:hover {\n    color: color-mix(in srgb, currentColor 65%, transparent);\n}\n\n\n.folderContainer:hover,\n#sidebar .allTasksBtn:hover,\n#sidebar .completedBtn:hover {\n    background-color: var(--clr-primary-semi-transparent);\n}\n\n#sidebar > button {\n    width: 200px;\n    border: none;\n    border-radius: 8px;\n    background-color: #00000000;\n    font-weight: bold;\n    padding: 10px;\n    margin-top: 8px;\n}\n\n#sidebar > button svg {\n    margin-right: 3px;\n}\n\n\n.folderContainer button {\n    border: none;\n    background-color: #00000000;\n    padding-right: 8px;\n    color: #5e5e5e;\n    font-size: 1rem;\n    font-weight: bold;\n    overflow-wrap: anywhere;\n}\n\nbody.darkmode .folderContainer button {\n    color: white;\n}\n\n.folderContainer button:first-child {\n    background-color: #00000000;\n    flex-grow: 1;\n    padding: 10px;\n}\n\n.folderContainer button:first-child svg {\n    flex-shrink: 0;\n    margin-right: 3px;\n    margin-top: 2px;\n}\n\n.myProjectDiv {\n    position: relative;\n    right: 70px;\n    top : 1px;\n    font-size: 12px;\n    font-weight: bold;\n    margin-top: 30px;\n}\n\n.menu {\n    position: relative;\n    right: 64px;\n    top : 1px;\n    font-size: 12px;\n    font-weight: bold;\n    margin-top: 50px;\n}\n\n/* header styling */\n\n#header {\n    display: flex;\n    flex-direction: column;\n    padding: 16px;\n    background-image: var(--header-image);\n}\n\n.darkModeToggle,\n.toggleSidebarBtn {\n    width: 40px;\n    height: 40px;\n    background-color: #00000000;\n    border: var(--clr-toggle-shade) solid 3px;\n    border-radius: 8px;\n    transition: margin 0.3s ease;\n}\n\n#header button svg {\n    width: 40px;\n    height: 40px;\n    rotate: var(--rotateIcon);\n}\n\n#header :last-child {\n    color: white;\n    font-size: 2.25rem;\n    font-weight: 400;\n    margin: 0;\n    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;\n}\n\n#header div {\n    color: #ffffff;\n    font-weight: 900;\n    margin-top: auto;\n    margin-bottom: 3px;\n}\n\n.headerBtnContainer {\n    display: flex;\n    gap: 1rem;\n    align-self: flex-end;\n    margin: 0 !important;\n}\n\n.toggleSidebarBtn:hover,\n.darkModeToggle:hover {\n    background-color: var(--clr-primary);\n    border: var(--clr-toggle-shade) 3px solid;\n    margin-top: -2px !important;\n}\n\n/* content body / card section styling */\n\n.tasks {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(min(600px, 100%), 1fr));\n    gap: 16px;\n    padding: 16px;\n}\n\n.taskCard {\n    border-radius: 8px;\n    display: flex;\n    align-items: center;\n    padding: 12px;\n    padding-left: 18px;\n    gap: 8px;\n    background-color: var(--clr-dark);\n}\n\nbody:not(.darkmode) .taskCard {\n    -webkit-box-shadow: 1px 1px 3px 0px rgba(0,0,0,0.36); \n    box-shadow: 1px 1px 3px 0px rgba(0,0,0,0.36);\n}\n\n.taskCard button {\n    background-color: #00000000;\n    outline: none;\n    border: none;\n    align-self: flex-start;\n}\n\n.taskCard button:hover {\n    color: color-mix(in srgb, currentColor 35%, transparent);\n}\n\n.taskCard .container {\n    margin-right: auto;\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n    margin-left: 16px;\n    overflow-wrap: anywhere;\n}\n\n.taskCard .container :first-child {\n    font-weight: 700;\n    font-size: 1.25rem;\n}\n\n.taskCard .container :first-child + div {\n   